import React, { useState, useEffect, useRef } from "react";
import axios from "axios";
import "./style.css";
import { toast, ToastContainer } from "react-toastify";
import Select from "react-select";
import { FaTimes } from "react-icons/fa";

const InsuranceLayer = () => {
  const [insuranceItems, setInsuranceItems] = useState([
    { title: "", document: null },
  ]);
  const [existingRecords, setExistingRecords] = useState([]);
  const [loading, setLoading] = useState(true);
  const userId = localStorage.getItem("userId");
    const [showDeleteModal, setShowDeleteModal] = useState(false);
     const [recordToDelete, setRecordToDelete] = useState(null);
       const [showInfoModal, setShowInfoModal] = useState(false);
     

  // Refs for file inputs
  const fileRefs = useRef([]);

  // Fetch existing insurance records
  const fetchInsuranceRecords = async () => {
    try {
      const res = await axios.get(
        `${process.env.REACT_APP_BACKEND_URL}/api/users/${userId}/insurance`
      );
      setExistingRecords(res.data || []);
    } catch (error) {
      console.error("Error fetching insurance records:", error);
    } finally {
      setLoading(false);
    }
  };

  useEffect(() => {
    fetchInsuranceRecords();
  }, [userId]);

  // Handle title change
  const handleTitleChange = (index, value) => {
    const updated = [...insuranceItems];
    updated[index].title = value;
    setInsuranceItems(updated);
  };

  // Handle file change
  const handleFileChange = (index, files) => {
    const file = files ? files[0] : null;
    if (file) {
      const file = files[0];

      // file.size is in bytes. 1 MB = 1024 * 1024 bytes
      const maxSize = 5 * 1024 * 1024; // 5 MB in bytes

      if (file.size > maxSize) {
        alert(
          `File size should not exceed 5 MB. Your file is ${(
            file.size /
            (1024 * 1024)
          ).toFixed(2)} MB`
        );
        if (fileRefs.current[index]) {
          fileRefs.current[index].value = ""; // clear file input
        }
        return;
      }
      const allowedFormats = /(\.jpg|\.jpeg|\.png|\.pdf|\.doc|\.docx)$/i;
      if (!allowedFormats.exec(file.name)) {
        toast.error(
          "File should be in .jpeg | .jpg | .png | .pdf | .doc | .docx format"
        );
        return;
      }
    }
    const updated = [...insuranceItems];
    updated[index].document = file;
    setInsuranceItems(updated);

    // Clear input if null
    if (!file && fileRefs.current[index]) {
      fileRefs.current[index].value = null;
    }
  };

  // Add new insurance item
  const addNewInsuranceItem = () => {
    setInsuranceItems((prev) => [...prev, { title: "", document: null }]);
  };

  // Remove insurance item
  const removeInsuranceItem = (index) => {
    setInsuranceItems((prev) => {
      if (prev.length === 1) {
        // Reset the only record instead of removing
        return [{ title: "", document: null }];
      }
      return prev.filter((_, i) => i !== index);
    });

    // Reset the file input too if it exists
    if (fileRefs.current[index]) {
      fileRefs.current[index].value = "";
    }
  };

  // Handle submit
  const handleSubmit = async (e) => {
    e.preventDefault();
    try {
      const formData = new FormData();
      insuranceItems.forEach((item) => {
        formData.append("title", item.title);
        if (item.document) {
          formData.append("document", item.document);
        }
      });

      await axios.post(
        `${process.env.REACT_APP_BACKEND_URL}/api/users/${userId}/insurance`,
        formData,
        {
          headers: { "Content-Type": "multipart/form-data" },
        }
      );

      // Refresh records after submission
      const res = await axios.get(
        `${process.env.REACT_APP_BACKEND_URL}/api/users/${userId}/insurance`
      );
      setExistingRecords(res.data.insurance || []);
      fetchInsuranceRecords();

      toast.success("Insurance information saved successfully!");

      // Reset form
      setInsuranceItems([{ title: "", document: null }]);
      fileRefs.current.forEach((ref) => {
        if (ref) ref.value = null;
      });
      fileRefs.current = [];
    } catch (error) {
      console.error(error);
      toast.error("Error saving insurance information.");
    }
  };

    const modalRef = useRef(null);
  
    useEffect(() => {
      // Handle click outside
      const handleClickOutside = (event) => {
        if (
          modalRef.current &&
          !modalRef.current.contains(event.target) &&
          showInfoModal
        ) {
          showInfoModal(false);
        }
      };
  
      document.addEventListener("mousedown", handleClickOutside);
  
      // ðŸ”‘ Scroll lock logic
      if (showInfoModal) {
        document.body.style.overflow = "hidden"; // Disable background scroll
      } else {
        document.body.style.overflow = "auto"; // Enable background scroll
      }
  
      return () => {
        document.removeEventListener("mousedown", handleClickOutside);
        document.body.style.overflow = "auto"; // Cleanup when component unmounts
      };
    }, [showInfoModal]);

    const handleDeleteClick = (record) => {
    setRecordToDelete(record);
    setShowDeleteModal(true);
      setShowInfoModal(true);
  };

    const handleDeleteConfirm = async () => {
    if (!recordToDelete) return;
    
    try {
      await axios.delete(
        `${process.env.REACT_APP_BACKEND_URL}/api/users/${userId}/insurance`,
        {
          data: { title: recordToDelete.title, document: recordToDelete.document },
        }
      );

      setExistingRecords(
        existingRecords.filter(
          (r) => r.title !== recordToDelete.title || r.document !== recordToDelete.document
        )
      );
      toast.success("Insurance record deleted successfully");
    } catch (error) {
      console.error("Error deleting record:", error);
      toast.error("Failed to delete insurance record");
    } finally {
      // Close modal
       setShowInfoModal(false);
      setShowDeleteModal(false);
      setRecordToDelete(null);
    }
  };

  // Close delete modal
  const handleDeleteCancel = () => {
    setShowDeleteModal(false);
    setRecordToDelete(null);
     setShowInfoModal(false);
  };

  // Handle deleting existing record
 
  

 const insuranceOptions = [
    {
      value: "National Insurance Co. Ltd.",
      label: "National Insurance Co. Ltd.",
    },
    {
      value: "Go Digit General Insurance Ltd.",
      label: "Go Digit General Insurance Ltd.",
    },
    {
      value: "Bajaj Allianz General Insurance Co. Ltd.",
      label: "Bajaj Allianz General Insurance Co. Ltd.",
    },
    {
      value: "Cholamandalam MS General Insurance Co. Ltd.",
      label: "Cholamandalam MS General Insurance Co. Ltd.",
    },
    {
      value: "Bharti AXA General Insurance Co. Ltd.",
      label: "Bharti AXA General Insurance Co. Ltd.",
    },
    {
      value: "HDFC ERGO General Insurance Co. Ltd.",
      label: "HDFC ERGO General Insurance Co. Ltd.",
    },
    {
      value: "Future Generali India Insurance Co. Ltd.",
      label: "Future Generali India Insurance Co. Ltd.",
    },
    {
      value: "The New India Assurance Co. Ltd.",
      label: "The New India Assurance Co. Ltd.",
    },
    {
      value: "Iffco Tokio General Insurance Co. Ltd.",
      label: "Iffco Tokio General Insurance Co. Ltd.",
    },
    {
      value: "Reliance General Insurance Co. Ltd.",
      label: "Reliance General Insurance Co. Ltd.",
    },
    {
      value: "Royal Sundaram General Insurance Co. Ltd.",
      label: "Royal Sundaram General Insurance Co. Ltd.",
    },
    {
      value: "The Oriental Insurance Co. Ltd.",
      label: "The Oriental Insurance Co. Ltd.",
    },
    {
      value: "Tata AIG General Insurance Co. Ltd.",
      label: "Tata AIG General Insurance Co. Ltd.",
    },
    {
      value: "SBI General Insurance Co. Ltd.",
      label: "SBI General Insurance Co. Ltd.",
    },
    {
      value: "Acko General Insurance Ltd.",
      label: "Acko General Insurance Ltd.",
    },
    {
      value: "Navi General Insurance Ltd.",
      label: "Navi General Insurance Ltd.",
    },
    {
      value: "Zuno General Insurance Ltd. (formerly Edelweiss)",
      label: "Zuno General Insurance Ltd. (formerly Edelweiss)",
    },
    {
      value: "ICICI Lombard General Insurance Co. Ltd.",
      label: "ICICI Lombard General Insurance Co. Ltd.",
    },
    {
      value: "Kotak Mahindra General Insurance Co. Ltd.",
      label: "Kotak Mahindra General Insurance Co. Ltd.",
    },
    {
      value: "Liberty General Insurance Ltd.",
      label: "Liberty General Insurance Ltd.",
    },
    {
      value: "Magma HDI General Insurance Co. Ltd.",
      label: "Magma HDI General Insurance Co. Ltd.",
    },
    {
      value: "Raheja QBE General Insurance Co. Ltd.",
      label: "Raheja QBE General Insurance Co. Ltd.",
    },
    {
      value: "Shriram General Insurance Co. Ltd.",
      label: "Shriram General Insurance Co. Ltd.",
    },
    {
      value: "United India Insurance Co. Ltd.",
      label: "United India Insurance Co. Ltd.",
    },
    {
      value: "Manipal Cigna Health Insurance Company Limited",
      label: "Manipal Cigna Health Insurance Company Limited",
    },
    {
      value: "Aditya Birla Health Insurance Co. Ltd.",
      label: "Aditya Birla Health Insurance Co. Ltd.",
    },
    {
      value: "Star Health & Allied Insurance Co. Ltd.",
      label: "Star Health & Allied Insurance Co. Ltd.",
    },
    {
      value: "MAX Bupa Health Insurance Company Ltd.",
      label: "MAX Bupa Health Insurance Company Ltd.",
    },
    {
      value: "Care Health Insurance Ltd.",
      label: "Care Health Insurance Ltd.",
    },
    {
      value: "Universal Sompo General Insurance Co. Ltd.",
      label: "Universal Sompo General Insurance Co. Ltd.",
    },
       {
      value: "Others",
      label: "Others",
    },
  ];

  return (
    <div className="container py-4">
      <div className="row">
        {/* Form Section */}
        <div className="col-lg-12 pe-lg-4 mb-5 mb-lg-0 px-4">
          <div className="card shadow-sm">
            <div className="card-header bg-black text-white">
              <h5 className="mb-0 text-white">Add Insurance/Health Card</h5>
            </div>
            <div className="card-body">
              <form onSubmit={handleSubmit}>
                {insuranceItems.map((item, index) => (
                  <div key={index} className="mb-4 border-bottom pb-3">
                    <div className="row g-3 medical-history">
                      <div className="col-5 col-md-5">
                        <label className="form-label fw-bold">
                          Insurance Title
                        </label>
                        <Select
                        className="f medical-input"
                          options={insuranceOptions}
                          value={
                            insuranceOptions.find(
                              (opt) => opt.value === item.title
                            ) || null
                          }
                          onChange={(selected) =>
                            handleTitleChange(
                              index,
                              selected ? selected.value : ""
                            )
                          }
                          placeholder="Select insurance..."
                          isClearable={true}
                          menuPortalTarget={document.body}
                          styles={{
                            menuPortal: (base) => ({ ...base, zIndex: 9999 }),
                            menuList: (base) => ({
                              ...base,
                              maxHeight: "250px",
                              overflowY: "auto",
                            }),
                          }}
                          required
                        />
                      </div>

                      <div className="col-5 col-md-5">
                        <label className="form-label fw-bold">
                          Upload Document
                        </label>
                        <input
                          type="file"
                          className="form-control medical-input"
                          ref={(el) => (fileRefs.current[index] = el)}
                          onChange={(e) =>
                            handleFileChange(index, e.target.files)
                          }
                          accept=".pdf,.doc,.docx,.jpg,.jpeg,.png"
                          required
                        />
                      </div>

                      {/* {item.document && (
                        <div className="col-12">
                          <div className="alert alert-light py-2 cross-remove-div">
                            <div className="d-flex justify-content-between align-items-center mb-2 cross-remove">
                              <span
                                className="text-truncate"
                                style={{ maxWidth: "200px" }}
                              >
                                <i className="far fa-file me-2"></i>
                                {item.document.name}
                              </span>
                              <button
                                type="button"
                                className="btn btn-sm btn-outline-danger"
                                onClick={() => handleFileChange(index, null)}
                              >
                                <i className="fas fa-times"></i>
                              </button>
                            </div>
                          </div>
                        </div>
                      )} */}

                      <div className="col-2 col-md-2 d-grid button-div" style={{ marginTop: "43px" }}>
                        <button
                          type="button"
                          className="btn btn-outline-danger mt-md-0 remove-btn-medical"
                          style={{ width: "140px", height: "40px" }}
                          onClick={() => removeInsuranceItem(index)}
                        >
                          <i className="fas fa-trash-alt me-2"></i>
                          Remove
                        </button>
                      </div>
                    </div>
                  </div>
                ))}

                <div className="d-flex flex-column flex-md-row justify-content-between gap-2">
                  <button
                    type="button"
                    className="btn btn-success w-md-auto"
                    onClick={addNewInsuranceItem}
                  >
                    <i className="fas fa-plus me-2"></i>
                    Add Another Insurance
                  </button>
                  <button type="submit" className="btn btn-primary w-md-auto">
                    <i className="fas fa-save me-2"></i>
                    Save Insurance
                  </button>
                </div>
              </form>
            </div>
          </div>
        </div>

        {/* Records Section */}
        <div className="col-lg-12 ps-lg-4 my-4 px-4">
          <div className="card shadow-sm">
            <div className="card-header bg-black text-white">
              <h5 className="mb-0 text-white">Your Insurance Records</h5>
            </div>
            <div className="card-body">
              {loading ? (
                <div className="text-center py-4">
                  <div className="spinner-border text-primary" role="status">
                    <span className="visually-hidden">Loading...</span>
                  </div>
                </div>
              ) : existingRecords.length === 0 ? (
                <div className="alert alert-info">
                  No insurance records found.
                </div>
              ) : (
                <div className="table-responsive">
                  <table className="table table-hover">
                    <thead>
                      <tr>
                        <th>Title</th>
                        <th>Document</th>
                        <th>Actions</th>
                      </tr>
                    </thead>
                    <tbody>
                      {existingRecords.map((record, index) => (
                        <tr key={`${record.title}-${index}`}>
                          <td style={{ color: "#000424" }}>{record.title}</td>
                          <td>
                            <a
                              href={record.document}
                              target="_blank"
                              rel="noopener noreferrer"
                              className="btn btn-sm btn-outline-primary"
                            >
                              <i className="fas fa-eye me-1"></i> View
                            </a>
                          </td>
                          <td>
                            <button
                              onClick={() => handleDeleteClick(record)}
                              className="btn btn-sm btn-outline-danger"
                            >
                              <i className="fas fa-trash-alt"></i>
                            </button>
                          </td>
                        </tr>
                      ))}
                    </tbody>
                  </table>
                </div>
              )}
            </div>
          </div>
        </div>
      </div>
          {/* Delete Confirmation Modal */}
      <div 
        className={`modal fade ${showDeleteModal ? 'show d-block' : ''}`} 
        style={{ backgroundColor: showDeleteModal ? 'rgba(0,0,0,0.5)' : 'transparent' }}
        tabIndex="-1"
      >
        <div className="modal-dialog modal-dialog-centered">
          <div className="modal-content">
            <div className="modal-header">
              <h5 className="modal-title">Confirm Delete</h5>
              <button 
          type="button" 
          className="btn p-1 position-absolute  end-0 m-3"
          onClick={handleDeleteCancel}
          style={{ 
            background: 'transparent',
            border: 'none',
            color: '#6c757d',
            fontSize: '1.2rem',
            // marginTop:'12px'
          }}
        >
          <FaTimes />
        </button>
            </div>
            <div className="modal-body">
              <p>Are you sure you want to delete this insurance record?</p>
              {recordToDelete && (
                <p className="fw-bold">Insurance: {recordToDelete.title}</p>
              )}
              <p className="text-danger">This action cannot be undone.</p>
            </div>
            <div className="modal-footer">
              <button 
                type="button" 
                className="btn btn-secondary" 
                onClick={handleDeleteCancel}
              >
                Cancel
              </button>
              <button 
                type="button" 
                className="btn btn-danger" 
                onClick={handleDeleteConfirm}
              >
                Delete
              </button>
            </div>
          </div>
        </div>
      </div>
      <ToastContainer position="top-right" autoClose={3000} />
    </div>
  );
};

export default InsuranceLayer;
